import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"
import { window, document } from "browser-monads"
import { useAllImagesHomeSlider } from "projectGraphqlStaticQueries/images-slider-home"
import * as S from "globalOrganisms/Slider/styles"

export default function Slider() {
  const allImages = useAllImagesHomeSlider()
  const sliderItems = [
    {
      largeImagePath: "assets/special-offers/carousel/002.jpg",
      smallImagePath: "assets/special-offers/carousel/002-md.jpg",
      link: "/offer-002",
    },
    {
      largeImagePath: "assets/special-offers/carousel/001.jpg",
      smallImagePath: "assets/special-offers/carousel/001-md.jpg",
      link: "/offer-001",
    },
    {
      largeImagePath: "assets/special-offers/carousel/003.jpg",
      smallImagePath: "assets/special-offers/carousel/003-md.jpg",
      link: "/offer-003",
    },
  ]

  useEffect(() => {
    document.querySelector(".carousel-item").classList.add("active")
  })

  return (
    <S.Slider
      id="carouselExampleControls"
      className="carousel slide"
      data-bs-ride="carousel"
    >
      <div className="carousel-inner">
        {sliderItems.map(item => {
          let largeImagePath = item.largeImagePath
          let itemLink = item.link
          return allImages.map(image => {
            let imageRelativePath = image.node.relativePath
            let imageFluid = image.node.childImageSharp.fluid
            if (imageRelativePath === largeImagePath)
              return (
                <Link to={itemLink} className="carousel-item" key={item.link}>
                  <Img
                    fluid={imageFluid}
                    key={imageRelativePath}
                    className="d-block w-100"
                  ></Img>
                </Link>
              )
            // if (imageRelativePath === smallImagePath && screenWidth < 768)
            //   return (
            //     <div className="carousel-item" key={item.link}>
            //       <Img
            //         fluid={imageFluid}
            //         key={imageRelativePath}
            //         className="d-block w-100"
            //       ></Img>
            //     </div>
            //   )
          })
        })}
      </div>
    </S.Slider>
  )
}
