import { environment, pages } from "projectConfig"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import ProductList from "globalOrganisms/ProductList"
import Slider from "globalOrganisms/SLider"
import Layout from "globalLayoutsIndexPage"

export default function IndexPage(props) {
  return (
    <>
      <GatsbySeo
        title={pages.index.title}
        description={pages.index.description}
        canonical={environment.siteUrl}
        openGraph={{
          url: props.location.href,
          title: pages.index.title,
          description: pages.index.description,
        }}
      />

      <Layout>
        <section style={{ marginBottom: "64px" }} className={"adaptive text"}>
          <Slider></Slider>
        </section>
        <section>
          <h2 className="adaptive text mb-3">Бонус-коды</h2>
          <ProductList category={"bonus-codes"} itemsLimit={5}></ProductList>
        </section>
        <section>
          <h2 className={"adaptive text mb-3"}>Рандомные аккаунты</h2>
          <ProductList
            category={"random-accounts"}
            itemsLimit={5}
          ></ProductList>
        </section>
      </Layout>
    </>
  )
}
