import { useStaticQuery, graphql } from "gatsby"
export const useAllImagesHomeSlider = () => {
  const { allFile } = useStaticQuery(
    graphql`
      query AllImages {
        allFile(
          filter: {
            relativePath: { regex: "/assets/special-offers/carousel//" }
          }
        ) {
          edges {
            node {
              relativePath
              name
              childImageSharp {
                fluid(
                  maxWidth: 1200
                  quality: 100
                  traceSVG: { color: "#8E8E93", blackOnWhite: true }
                ) {
                  ...GatsbyImageSharpFluid_withWebp_tracedSVG
                }
              }
            }
          }
        }
      }
    `
  )
  return allFile.edges
}
